import { Button, Grid, TextField, Typography } from "@mui/material";
import HeaderWrapper from "components/Wrapper/HeaderWrapper";
import React from "react";
import { makeStyles } from "@mui/styles";
import { useFormik } from "formik";
import * as yup from "yup";
import { AI_forgot_password } from "API/userAPI";
import { useAIEventHandler, useInstance } from "easy-redux-http-call";
import { enqueueSnackbar } from "notistack";
import { useNavigate } from "react-router";

const useStyles = makeStyles((theme) => ({
  dmForgotPasswordRoot: {
    padding: 15,
    marginTop: 60,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  dmForgotPasswordFormContainer: {
    width: "100%",
    "&>p": {
      color: theme.palette.grey[500],
      marginTop: 5,
    },
  },
  forgotPasswordForm: {
    marginTop: 10,
    "&>div": {
      marginTop: 10,
    },
    "&>button": {
      marginTop: 10,
      justifyContent: "space-between",
    },
  }
}));

export default function ForgotPassword() {
  const classes = useStyles();
  const navigate = useNavigate();

  const forgotPasswordState = useInstance(AI_forgot_password)

  const handleForgotPassword = (values) => {
    AI_forgot_password.call({
      params: values
    })
  }

  useAIEventHandler(AI_forgot_password, {
    onSuccess: (data) => {
      enqueueSnackbar(data?.data?.message, { variant: 'success' })
      navigate('/login')
    },
    onError: (error) => {
      enqueueSnackbar(error?.error?.message, { variant: 'error' })
    }
  })

  const formik = useFormik({
    initialValues: {
      email: "",
    },
    validationSchema: yup.object({
      email: yup.string().email("Invalid email address").required("Email is required"),
    }),
    onSubmit: handleForgotPassword
  });

  const { handleSubmit, handleChange, values, errors, touched } = formik;

  return (
    <HeaderWrapper>
      <Grid className={classes.dmForgotPasswordRoot}>
        <Grid className={classes.dmForgotPasswordFormContainer}>
          <Typography variant="h4" fontWeight="600">Forgot Password ?</Typography>
          <Typography variant="body2">Enter the email address you used to register with.</Typography>
          <form className={classes.forgotPasswordForm} onSubmit={handleSubmit}>
            <TextField
              required
              fullWidth
              autoFocus
              label="Email"
              variant="outlined"
              type="email"
              placeholder="Enter your email"
              name="email"
              value={values.email}
              onChange={handleChange}
              error={errors.email && touched.email}
              helperText={errors.email && touched.email ? errors.email : null}
            />

            <Button
              fullWidth
              variant="contained"
              type="submit"
              size="large"
              disabled={forgotPasswordState.loading}
            >
              Submit
            </Button>
          </form>
        </Grid>
      </Grid>
    </HeaderWrapper>
  );
}
